import {
    AppBar,
    Badge,
    Button,
    Toolbar
} from '@material-ui/core';
import React, { useEffect,useState } from 'react';
// import { useAuth0 } from "@auth0/auth0-react";

import { ThemeProvider } from '@material-ui/core/styles';
import logo from '../../../assets/img/logo.png';
import navStyles,{theme as Theme} from './navigationStyle'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
const CartCount = 4;
const NavigatonBar = (props) => {
    const classes = navStyles();
    const [user_details, setDetails] = useState({
        name:"",
    })
    // const { user, isAuthenticated, isLoading } = useAuth0();
    useEffect(()=>{
        let userName = localStorage.getItem('user_name') as string;
        // isAuthenticated && localStorage.setItem("user_name",user.name);
        if(userName !== "undefined"){
            
        setDetails({
                ...user_details,
                name:userName
            })
        };
    },[])
    return (
        <ThemeProvider theme={Theme}>
        <div>
        <AppBar className={classes.root} position='fixed' style={{ background: props.backGround,boxShadow:props.shadow}}>
            <Toolbar>
                <img src={logo} alt='Company name' className={classes.img} />
                <div className={classes.grow} />
                <div className={classes.sectionDesktop}>
                    <Button className={classes.button}>About us</Button>
                    <Button className={classes.button}>Services <ExpandMoreIcon /></Button>
                    <Button className={classes.button}>Blog</Button>
                    <Button className={classes.button} href='/category/cart'>
                        <Badge badgeContent={CartCount} color="primary">
                            <ShoppingCartIcon />
                        </Badge>
                    </Button>
                    {
                        user_details.name !== "undefined"?
                        <Button className={classes.button} href="/home/user-profile">{user_details.name}</Button>:
                        <Button className={classes.button} href="/login">{"Sign in/Sign up"}</Button>
                    }
                   
                </div>
            </Toolbar>
        </AppBar>
    </div>
    </ThemeProvider>)

}
export default NavigatonBar;